import React from 'react'
import './header.css'
import { IoLogoDocker } from "@react-icons/all-files/io5/IoLogoDocker";
import {HiOutlineSearch} from "@react-icons/all-files/hi/HiOutlineSearch"
import {RiShoppingBasketFill} from "@react-icons/all-files/ri/RiShoppingBasketFill";
import {BsFillPersonFill} from "@react-icons/all-files/bs/BsFillPersonFill";
import {FiMenu} from "@react-icons/all-files/fi/FiMenu";


export default function Header() {
  return (
    <header className="header">
        <div className="logo-box">
            <a href="#" className='logo-icon-link'>
                <IoLogoDocker className='logo-cion'/>
            </a>
            <a href="#" className="logo-text-link">
             E-Shop
            </a>
        </div>
        <nav className="main-nav">
            <ul className="main-nav-list">
                <li>
                    <a href="#" className="main-nav-link">Men</a>
                </li>
                <li>
                    <a href="#" className="main-nav-link">Women</a>
                </li>
                <li>
                    <a href="#" className="main-nav-link">Kid</a>
                </li>
            </ul>
        </nav>
        <div className="nav-icons">
            <ul className="nav-icon-list">
                <li>
                    <a href="#" className="nav-icon-link">
                    <HiOutlineSearch className='nav-icon'/>
                    </a>
                </li>
                <li>
                    <a href="#" className="nav-icon-link">
                    <RiShoppingBasketFill className='nav-icon'/>
                    </a>
                </li>
                <li>
                    <a href="#" className="nav-icon-link">
                    <BsFillPersonFill className='nav-icon'/>
                    </a>
                </li>
            </ul>  
        </div>
        <button className='btn-mobile-nav'>
            <FiMenu className='btn-mobile-nav-icon'/>
        </button>
    </header>
  )
}
