import React from 'react'
import './hero.css'
import {BsArrowRight} from '@react-icons/all-files/bs/BsArrowRight'
import {BsArrowLeft} from '@react-icons/all-files/bs/BsArrowLeft'
import {FiUmbrella} from '@react-icons/all-files/fi/FiUmbrella'
import {FiUnlock} from '@react-icons/all-files/fi/FiUnlock'
import {FiPieChart} from '@react-icons/all-files/fi/FiPieChart'

export default function Hero() {
  return (
  
      <div className="hero">
        <div className="hero-title">
           <div className="slider-bar">
            <p>01</p>
            <div className="slider-line">
              <div className="scroll-line"></div>
            </div>
            <p>05</p>
           </div>
            <div>
              <h1 className="hero-text">
              Sale of the summer collection
              </h1>
              <div className="shop-now">
                <a href="#" className='shop-now-link'>
                <BsArrowRight className='shop-now-btn'/>
                </a>
              </div>
            </div>
        </div>
     
        
        <div className="hero-info">
          <ul className="hero-info-list">
            <li className="hero-info-item">
              <FiUmbrella className='info-item-icon'/>
              <div className='info-item-text'>
                <h3 className="info-item-title">Free Shipping</h3>
                <p>On purchases over $199</p>
              </div>
            </li>
            <li className="hero-info-item">
              <FiPieChart className='info-item-icon'/>
              <div className='info-item-text'>
                <h3 className="info-item-title">99% Satisfied Customers</h3>
                <p>Our clients' opinions speak for themselves</p>
              </div>
            </li>
            <li className="hero-info-item">
              <FiUnlock className='info-item-icon'/>
              <div className='info-item-text'>
                <h3 className="info-item-title">Originality Guaranteed</h3>
                <p>30 days warranty for each product from our store</p>
              </div> 
            </li>
          </ul>
        </div>
        <div className="slider-btns">
          <div className="slider-btn-left">
            <a href="#" className='slider-btn-link'>
            <BsArrowLeft className='slider-icon'/>
            </a>
          </div>
          <div className="slider-btn-right">
          <a href="#" className='slider-btn-link'>
            <BsArrowRight className='slider-icon'/>
            </a>
          </div>
        </div>
        
      </div>
 
        
    
   
  )
    
  
}
