import React from 'react'
import './sectionSelect.css'
import Select1 from '../assets/img/select1.png'
import Select2 from '../assets/img/select2.png'
import Select3 from '../assets/img/select3.png'
import Select4 from '../assets/img/select4.png'

export default function SectionSelect() {
  return (
    <div className="section-select">
        <div className="select-header">
            <h1 className="just-for-you">
                Select just for you
            </h1>
             <a href="#" className='select-btn btn'>
                  Show more
              </a>        
        </div>
        <div className="select-grid grid">

          <div className="grid-item">
            <div className="grid-item-img-1">
              <img src={Select1} alt="select-img" />
            </div>
            <div className="grid-item-box">
              <div className="grid-item-title">
                Loose Knit 3/4 Sleeve
              </div>
              <div className="grid-item-price">
                $199.99
              </div>
            </div>
          </div>
          
          <div className="grid-item">
            <div className="grid-item-img-2">
            <img src={Select2} alt="select-img" />
            </div>
            <div className="grid-item-box">
              <div className="grid-item-title">
                Basic Slim Fit T-Shirt
              </div>
              <div className="grid-item-price">
                <p className='grid-item-firstprice'>$ 79.99</p>
                <p className='grid-item-sale'>$ 119.99</p>
              </div>
            </div>
          </div>

          <div className="grid-item">
            <div className="grid-item-img-3">
            <img src={Select3} alt="select-img" />
            </div>
            <div className="grid-item-box">
              <div className="grid-item-title">
                T-Shirt Summer Vibes
              </div>
              <div className="grid-item-price">
                $89.99
              </div>
            </div>
          </div>
          
          <div className="grid-item">
            <div className="grid-item-img-4">
            <img src={Select4} alt="select-img" />
            </div>
            <div className="grid-item-box">
              <div className="grid-item-title">
                Loose Textured T-Shirt
              </div>
              <div className="grid-item-price">
                $49.99
              </div>
            </div>
          </div>

        </div>
    </div>
  )
}
