import React from 'react'
import './sectionSubscribe.css'


export default function SectionSubscribe() {
  return (
    <div className="section-subscribe">
        <h1 className="subscribe-title">
          Subscribe to our newsletter and receive exclusive offers every week
        </h1>
        <div className="subscribe-input-btn">
            <input className="subscribe-input" type="email" placeholder='Enter your email' />
            <a href="#" className='subscribe-btn btn'>Subscribe</a>
        </div>
    </div>
  )
}
